    constructor(component: EditorComponent) {
        this.documentEditorComponent = component;
    }
    public propertyChange: Function;
    public triggerPropertyChange(propertyName: string, propertyValue: Object): void {
        if (!isNullOrUndefined(this.propertyChange)) {
            this.propertyChange({ propertyName: propertyName, propertyValue: propertyValue });
        }
    }
    updateOptions(args: any): void {
        this.updateStyleNames();
        args.popup.element.getElementsByClassName('create-style-footer')[0].addEventListener('click', this.createStyle.bind(this));
    }
    updateStyleNames() {
        this.styleName = !isNullOrUndefined((this.documentEditorComponent.tableStyleDropDown as any).itemData) ? (this.documentEditorComponent.tableStyleDropDown as any).itemData.StyleName : undefined;
        this.stylesData = this.constructStyleDropItems(this.documentEditorComponent.documentEditor.getStyles('Paragraph'));
    }
    createStyle() {
        this.documentEditorComponent.documentEditor.showDialog('Styles')
    }
    constructStyleDropItems(styles: any) {
        let collection: any = [];
        for (let styleObj of styles) {
            let obj: any = {};
            obj.StyleName = (styleObj as any).name;
            obj.Style = this.parseStyle((styleObj as any).style as string);
            collection.push(obj);
        }
        return collection;
    }
    parseStyle(style: string): string {
        let domStyle: any = {};
        let styleObj: any = JSON.parse(style);
        let textDecoration: string = '';
        if (!isNullOrUndefined(styleObj.characterFormat.baselineAlignment) && styleObj.characterFormat.baselineAlignment !== 'Normal') {
            let vAlign: string = '';
            switch (styleObj.characterFormat.baselineAlignment) {
                case 'Superscript':
                    vAlign = "super";
                    break;
                case 'Subscript':
                    vAlign = "sub";
                    break;
            }
            if (vAlign.length > 1) {
                domStyle["vertical-align"] = vAlign;
            }
        }
        if (!isNullOrUndefined(styleObj.characterFormat.underline) && styleObj.characterFormat.underline !== 'None') {
            textDecoration += "underline ";
        }
        if (!isNullOrUndefined(styleObj.characterFormat.strikethrough) && styleObj.characterFormat.strikethrough !== 'None') {
            textDecoration += "line-through ";
        }
        if (!isNullOrUndefined(styleObj.characterFormat.fontSize)) {
            domStyle["font-size"] = (styleObj.characterFormat.fontSize + 'px');
        }
        if (!isNullOrUndefined(styleObj.characterFormat.fontFamily)) {
            domStyle["font-family"] = styleObj.characterFormat.fontFamily;
        }
        if (!isNullOrUndefined(styleObj.characterFormat.bold) && styleObj.characterFormat.bold) {
            domStyle["font-weight"] = "bold";
        }
        if (!isNullOrUndefined(styleObj.characterFormat.italic) && styleObj.characterFormat.italic) {
            domStyle["font-style"] = "italic";
        }
        if (!isNullOrUndefined(styleObj.characterFormat.fontColor)) {
            domStyle["color"] = styleObj.characterFormat.fontColor;
        }
        if (textDecoration.length > 1) {
            domStyle["text-decoration"] = textDecoration;
        }
        return domStyle;
    }
    closeStyleValue(): void {
        if (!isNullOrUndefined(this.styleName)) {
            this.documentEditorComponent.tableStyleDropDown.value = this.styleName;
            this.documentEditorComponent.tableStyleDropDown.dataBind();
        }
    }
    selectStyleValue(args: any): void {
        if (!args.isInteracted) {
            return;
        }
        setTimeout(() => { this.applyStyleValue(args); }, 10);
    }
    applyStyleValue(args: any) {
        if (!this.documentEditorComponent.documentEditor.isReadOnly && this.documentEditorComponent.documentEditor.editor) {
            this.documentEditorComponent.documentEditor.editor.applyStyle(args.itemData.StyleName);
        }
    }
    closeBullets() {
        document.getElementById('table_bullet_list').style.display = 'none';
    }
    openBullets() {
        document.getElementById('table_bullet_list').style.display = 'block';
    }
    closeNumbering() {
        document.getElementById('table_numbering_list').style.display = 'none';
    }
    openNumbering() {
        document.getElementById('table_numbering_list').style.display = 'block';
    }
    lineSpacingAction(args: any) {
        let text: string = args.item.text;
        switch (text) {
            case 'Single':
                this.documentEditorComponent.documentEditor.selection.paragraphFormat.lineSpacing = 1;
                break;
            case '1.15':
                this.documentEditorComponent.documentEditor.selection.paragraphFormat.lineSpacing = 1.15;
                break;
            case '1.5':
                this.documentEditorComponent.documentEditor.selection.paragraphFormat.lineSpacing = 1.5;
                break;
            case 'Double':
                this.documentEditorComponent.documentEditor.selection.paragraphFormat.lineSpacing = 2;
                break;
        }
        setTimeout(() => { this.documentEditorComponent.documentEditor.focusIn(); }, 30);
    }
    bulletSplitObjClick() {
        this.applyLastAppliedBullet();
    }
    applyLastAppliedBullet() {
        switch (this.documentEditorComponent.appliedLastBullet) {
            case 'dot':
                this.dotClick();
                break;
            case 'square':
                this.squareClick();
                break;
            case 'circle':
                this.circleClick();
                break;
            case 'flower':
                this.flowerClick();
                break;
            case 'tick':
                this.tickClick();
                break;
            case 'arrow':
                this.arrowClick();
                break;
        }
    }
